  display: flex;
  flex-direction: column;
  padding: 40px 0;
}
.card-jobs > .inscription-jobs {
  font-family: GinoraSans-regular, sans-serif;
  font-size: 12px;
  color: #B8B8B8;
  line-height: 18px;
  padding-bottom: 10px;
  letter-spacing: 4px;
  opacity: 0.7;
}

.link-card-jobs {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.link-card-jobs > .card-title-jobs {
  font-family: GinoraSans-regular, sans-serif;
  font-size: 26px;
  color: #F4F4F4;
  line-height: 32px;
}
.link-card-jobs > .card-title-jobs:hover {
  background: linear-gradient(92.73deg, #99A9FF 0%, #5CE4CF 100%);
  background-clip: text;
  color: transparent;
}
.link-card-jobs > .active-title-jobs {
  background: linear-gradient(92.73deg, #99A9FF 0%, #5CE4CF 100%);
  background-clip: text;
  color: transparent;
}

.desc-jobs {
  font-family: Gilroy-medium, sans-serif;
  font-size: 12px;
  color: #B8B8B8;
  line-height: 16px;
  padding-top: 20px;
  opacity: 0.7;
}

.link-jobs-card {
  display: flex;
  align-items: center;
  padding: 60px 0 100px;
}
.link-jobs-card:hover > .text-jobs {
  color: #5CE4CF;
}
.link-jobs-card:hover > .arrow-right-green {
  color: #99A9FF;
}
.link-jobs-card > .text-jobs {
  font-family: GinoraSans-semi-bold, sans-serif;
  font-size: 16px;
  color: #99A9FF;
  padding-right: 10px;
}
.link-jobs-card > .arrow-right-green {
  width: 25px;
  height: 12px;
  color: #5CE4CF;
}

.title-resources {
  font-family: GinoraSans-regular, sans-serif;
  font-size: 36px;
  color: #F4F4F4;
  line-height: 44px;
  padding: 20px 0 60px;
}

.cards-resources {
  padding: 0 20px;
}

.date {
  font-family: Gilroy-regular, sans-serif;
  font-size: 12px;
  color: #B8B8B8;
  line-height: 16px;
  display: inline-block;
  padding: 30px 0 10px;
}

.title-card-resources {
  font-family: GinoraSans-regular, sans-serif;
  font-size: 26px;
  color: #B8B8B8;
  line-height: 32px;
  padding-bottom: 60px;
}
.title-card-resources:hover {
  background: linear-gradient(96.97deg, #99A9FF 20.47%, #5CE4CF 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.active-card-resources {
  background: linear-gradient(96.97deg, #99A9FF 20.47%, #5CE4CF 100%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.swiper-nav {
  display: flex;
  justify-content: end;
  padding-bottom: 60px;
}

.box-btn-right {
  width: 62px;
  height: 8px;
  display: inline-flex;
  color: #1D1D1D;
  background: linear-gradient(to right, #99A9FF, #5CE4CF);
}
.box-btn-right > .arrow-btn-right {
  width: 100%;
  height: 100%;
}

.box-btn-left {
  width: 31px;
  height: 8px;
  display: inline-flex;
  margin-right: 21px;
  color: #1D1D1D;
  background: linear-gradient(to left, #99A9FF, #5CE4CF);
}

.arrow-btn-left {
  width: 100%;
  height: 100%;
}

.swiper-button-disabled.box-btn-right {
  background: linear-gradient(to right, rgba(92, 228, 207, 0.15), rgba(153, 169, 255, 0.4));
}

.swiper-button-disabled.box-btn-left {
  background: linear-gradient(to left, rgba(92, 228, 207, 0.15), rgba(153, 169, 255, 0.4));
}

.link-resources {
  display: flex;
  align-items: center;
  padding: 20px 0 100px;
}
.link-resources:hover > .text-resources {
  color: #5CE4CF;
}
.link-resources:hover > .arrow-right-green {
  color: #99A9FF;
}
.link-resources .text-resources {
  font-family: GinoraSans-semi-bold, sans-serif;
  font-size: 16px;
  color: #99A9FF;
  padding-right: 10px;
}
.link-resources > .arrow-right-green {
  width: 25px;
  height: 12px;
  color: #5CE4CF;
}

.title-reg {
  width: 330px;
  font-family: GinoraSans-regular, sans-serif;
  font-size: 36px;
  color: #F4F4F4;
  line-height: 44px;
  background: conic-gradient(from 180deg at 50% 50%, #66FFE6 -160.13deg, #8093FF 42.37deg, #66FFE6 199.87deg, #8093FF 402.37deg);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.desc-reg {
  font-family: Gilroy-regular, sans-serif;
  font-size: 16px;
  color: #B8B8B8;
  line-height: 26px;
  padding: 20px 0 60px;
}

.group-message {
  display: flex;
  flex-direction: column;
}

.group-name,
.group-email {
  display: flex;
  flex-direction: column;
  position: relative;
}
.group-name::before,
.group-email::before {
  width: 80px;
  height: 3px;
  content: "";
  display: block;
  position: absolute;
  top: 38px;
  left: 10px;
  background: linear-gradient(90deg, #99A9FF 0%, #5CE4CF 100%);
}

.label-name,
.label-email,
.text-message {
  font-family: Gilroy-regular, sans-serif;
  font-size: 14px;
  color: #B8B8B8;
  line-height: 16px;
  padding-left: 10px;
}

.name-text,
.email-text {
  height: 24px;
}

.name-text,
.email-text,
.textarea {
  margin-bottom: 40px;
  padding: 0 10px;
  border: 1px solid #1D1D1D;
  border-bottom-color: #565656;
  background-color: #1D1D1D;
  color: #B8B8B8;
}

.input-sizer {
  display: inline-grid;
  vertical-align: top;
  align-items: center;
  position: relative;
}

.input-sizer.stacked {
  align-items: stretch;
}

.input-sizer.stacked::after,
.input-sizer.stacked input,
.input-sizer.stacked .textarea {
  grid-area: 2/1;
}

.input-sizer::after,
.input-sizer input,
.input-sizer .textarea {
  width: auto;
  min-width: 1em;
  grid-area: 1/2;
  font: inherit;
  margin: 0;
  resize: none;
  background: none;
  appearance: none;
}

.input-sizer .textarea {
  min-height: 20px;
}

.input-sizer::after {
  content: attr(data-value) " ";
  visibility: hidden;
  white-space: pre-wrap;
}

.bottom-line {
  width: 80px;
  height: 3px;
  position: relative;
  left: 10px;
  bottom: 2px;
  margin-bottom: 40px;
  background: linear-gradient(90deg, #99A9FF 0%, #5CE4CF 100%);
}

.reg-btn {
  display: flex;
  align-items: center;
  padding: 20px 0 80px;
  border: none;
  background-color: transparent;
}
.reg-btn > .reg-text-btn {
  font-family: GinoraSans-semi-bold, sans-serif;
  font-size: 16px;
  color: #99A9FF;
}
.reg-btn > .arrow-right-green {
  width: 25px;
  height: 12px;
  margin-left: 30px;
  color: #5CE4CF;
}
.reg-btn:hover > .reg-text-btn {
  color: #5CE4CF;
}
.reg-btn:hover > .arrow-right-green {
  color: #99A9FF;
}

.networks-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  padding-bottom: 40px;
}

.item-networks {
  padding: 0 10px 20px;
}
.item-networks > .network-link {
  font-family: GinoraSans-regular, sans-serif;
  font-size: 12px;
  color: #B8B8B8;
  line-height: 18px;
}
.item-networks > .network-link:hover {
  color: #99A9FF;
}
.item-networks > .active-network-link {
  color: #99A9FF;
}

.basement {
  display: flex;
  justify-content: center;
  align-items: center;
}
.basement > .auth-protection {
  padding: 30px 0 35px;
  color: #B8B8B8;
}
.basement > .company-protection {
  margin: 0 0 2px 4px;
  padding-bottom: 2px;
  border-bottom: 2px solid #99A9FF;
  border-bottom-width: 1.5px;
  color: #99A9FF;
}
.basement > .auth-protection,
.basement > .company-protection {
  font-family: GinoraSans-regular, sans-serif;
  font-size: 16px;
  line-height: 16px;
